<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList"
          >
            <el-button
              @click="getOrderList"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!---->
      <el-table :data="orderList"  border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === 1"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          :formatter="formatterIsSend"
          prop="Is_send"
        >
        </el-table-column>
        <el-table-column label="" prop="creat_time">
          <template slot-scope="scope">
            {{ scope.row.creat_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!---->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.order_id)"
            ></el-button>
            <!---->
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgressBox(scope.row.order_number)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressClosed"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            :props="{
            expandTrigger: 'hover',}"
            change-on-select
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="addressVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--timeline-->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time|dateFormat"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      addressVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      progressVisible: false,
      progressInfo: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: res } = await this.$http.get('orders/list', {
        params: this.queryInfo
      })
      if (!res.success) return this.$message.error(res.msg)
      this.orderList = res.response.data
      this.total = res.response.dataCount
    },
    // 
    // 
    formatterIsSend: function (row, column) {
      return row.is_send == 1 ? '' : ''
    },
    // 
    handleSizeChange (newsize) {
      this.queryInfo.pagesize = newsize
      this.getOrderList()
    },
    // 
    handleCurrentChange (newnum) {
      this.queryInfo.pagenum = newnum
      this.getOrderList()
    },
    // 
    showEditDialog () {
      this.addressVisible = true
    },
    // 
    addressClosed () {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async showProgressBox (num) {
      const { data: res } = await this.$http.get('logisticsprogresss/kuaidi', {
        params: { order_number: num }
      })
      if (!res.success) return this.$message.error(res.msg)
      this.progressInfo = res.response
      this.progressVisible = true
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';
.el-cascader {
  width: 100%;
}
</style>
